import { Router } from '@angular/router';
import { UserService } from './../../shared/user.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.css']
})
export class LoginFormComponent implements OnInit {
  formModel = {
    UserName: "",
    Password: ""
  }

  constructor(private servise: UserService,
    private router: Router) { }

  ngOnInit(): void {
    if(localStorage.getItem('token')) this.router.navigateByUrl('/home');
  }

  submit(){
    console.log(this.formModel)
    this.servise.login(this.formModel).subscribe((res: any) => {
      localStorage.setItem('token', res.token);
      this.router.navigateByUrl('/home')
    },
    err => console.log(err)) //dar reiks pridet klaidos pranesima
  }

}
