import { ExerciseDetail } from './../../shared/models/exercise-detail.model';
import { Component, OnInit, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { MusculeGroup } from 'src/app/enumerations/musculeGroup';
import { ExerciseDetailService } from 'src/app/shared/exercise-detail.service';
import { WorkoutDetailService } from 'src/app/shared/workout-detail.service';

@Component({
  selector: 'addexer-list',
  templateUrl: './addexer-list.component.html',
  styleUrls: ['./addexer-list.component.css']
})
export class AddexerListComponent implements OnInit {
  groups=MusculeGroup;
  filter: any = {};

  constructor(private exerciseService: ExerciseDetailService,
    private workoutServise: WorkoutDetailService, 
    private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    this.exerciseService.refreshList();
    this.cdr.detectChanges()
  }

  get Exercises(): ExerciseDetail[] {
    var list = this.exerciseService.list
    if(this.filter.Index != null) list = list.filter(e => e.MusculeGroup == this.filter.Index)
    return list
  }

  AddExer(id, name/*: workoutExercise*/){
    this.workoutServise.AddExercise(id, name)
  }

  onFilterChange(){
    console.log(this.filter.Index)
    this.cdr.detectChanges()
  }

  resetFilter(){
    this.filter={};
    this.onFilterChange()
  }

}
